.menu {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.menu .menuTitle {
  font-family: cursive;
  font-size: 60px;
}
.subName {
  position: static;
}
.structName {
  font-size: 50px;
  text-shadow: 0 0 3px #00013d, 0 0 5px #0004ff;
  font-weight: 1000;
  color: white;
}

.menuList,
.menuList2 {
  width: 70vw;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}
.menuList div {
  border : 1px rgb(72, 187, 253) solid;;
}
.menuList2 {
  padding-top: 30px;
}
.menuList2 div {
  border: 1px rgb(104, 196, 249) solid;
}

/* MENU ITEM STYLING */

.menuItem {
  background-color: rgb(237, 246, 252);
  border-radius: 15px;
  width: 300px;
  height: 330px;
  margin: 20px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

.menuItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
  cursor: pointer;
}

.menuItem div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.menuItem h1,
.menuItem p {
    text-align: center;
    color: rgb(0, 132, 255) ;
}

@media only screen and (max-width: 1450px) {
 
  .menuItem {
    width: 270px;
    height: 300px;
  }
  .menuItem div {
    height: 190px;
  }
}
@media only screen and (max-width: 1200px) {

  .menuItem {
    width: 240px;
    height: 270px;
  }
  .menuItem div {
    height: 180px;
  } 
  .menuItem h1 {
    font-size: 25px;
  }
}


@media only screen and (max-width: 1050px) {
  .menuList,
  .menuList2 {
    grid-template-columns: 1fr 1fr;
  }
  .menuItem {
    width: 270px;
    height: 300px;
  }
  .menuItem div {
    height: 210px;
  }
}

@media only screen and (max-width: 800px) {

  .menuItem {
    width: 210px;
    height: 250px;
  }

  .menuItem div {
    height: 170px;
  }
}
@media only screen and (max-width: 650px) {
.menuItem {
    width: 180px;
    height: 230px;
  }

  .menuItem div {
    height: 160px;
  }
  .menuItem h1 {
    font-size: 20px;
  }
}
@media only screen and (max-width: 550px) {
  .menuItem {
      width: 150px;
      height: 200px;
    }
  
    .menuItem div {
      height: 145px;
    }
  
    .menuItem h1 {
      font-size: 16px;
    }
}
@media only screen and (max-width: 450px) {
  .menuItem {
    width: 120px;
    height: 170px;
  }

  .menuItem div {
    height: 120px;
  }

  .menuItem h1 {
    font-size: 14px;
  }
  .menuList2 {
    padding-top: 10px
  }
  .structName {
    font-size: 35px;
  }
}

@media only screen and (max-width: 400px) {
  .menuItem {
    width: 90px;
    height: 135px;
  }

  .menuItem div {
    height: 85px;
  }

  .menuItem h1 {
    font-size: 12px;
  }
  .menu .menuTitle {
    font-size: 50px;
  }
}
@media only screen and (max-width: 350px) {
  .menuList,
  .menuList2 {
    width: 90vw;
  }
}
@media only screen and (max-width: 315px) {
  .menu, 
  .home,
  .contact {
    margin-top: 50px;
  }
}