.navbar {
  width: 100%;
  height: 100px;
  background-color: rgb(4, 169, 224);
  display: flex;
  flex-direction: row;
}

.navbar .leftSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  padding-left: 10px;
  padding-right: 0;
}
.navbar .leftSide img {
  width: 80px;
}
.navbar .leftSide .name {
  color: white;
}

.navbar .rightSide {
  flex: 50%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}


.navbar a {
  color: white;
  text-decoration: none;
  margin: 20px;
}
.navbar a:hover {
  opacity: 70%;
}

.navbar .rightSide button {
  background-color: transparent;
  border: none;
  color: white;
  cursor: pointer;

}

.navbar #open {
  padding-left: 0px;
} 

.navbar #open img {
  display: none;
}

.navbar #close img {
  display: inherit;
}

.navbar #open .hiddenLinks { 
  display: none;
  margin-left: 30px;
}

.navbar #close .hiddenLinks {
  display: none;
}

@media only screen and (max-width: 900px) {
  .navbar .rightSide a {
width: 70px;
  }
  .navbar .rightSide {
    padding-left: 70px;
  }
     .navbar .leftSide {
       padding-left: 60px;
     }
}

@media only screen and (max-width: 700px) {
  .navbar .rightSide a {
    display: none;
  }
  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 50px;
  }
  .navbar .rightSide button {
    display: none;  
  }
    .navbar .leftSide {
      padding-left: 30px;
    }
    .navbar .leftSide .name {
      display: none;
    }
    .navbar #open img {
      display:inherit;
    }
    .navbar #close img {
      display: inherit;
    }
    .navbar #open .hiddenLinks {
      display: inherit;
      margin-left: 0;
      font-size: 15px;
    }
        .navbar #close .hiddenLinks {
          display: inherit;
        }
}

@media only screen and (min-width: 700px) {
  .hiddenLinks {
    display: none;
  }
}

@media only screen and (min-width: 700px) and (max-width: 9009px){
  .navbar #open img {
    display: inherit;
  }
     .navbar .rightSide button {
       display: none;
     }
}
@media only screen and (min-width: 580px) and (max-width: 700px) {
  .navbar #open .hiddenLinks {
    margin-left: 150px;
  }
  .navbar img {
    margin-left: 25px;
  }
}
@media only screen and (max-width: 550px) {
  .navbar .rightSide button {
    display: none;
  }

  .navbar .leftSide #open .hiddenLinks {
    display: inherit;
  }
  .navbar .leftSide img {
    width: 75px;
  }
}
@media only screen and (max-width: 400px) {
  .navbar .leftSide img {
    width: 65px;
  }
}
@media only screen and (max-width: 350px) {
  .navbar .leftSide img {
    width: 55px;
  }
}
@media only screen and (max-width: 315px) {
  .navbar {
    position: fixed;
    height: 10%;
  }
 .leftSide .hiddenLinks {
  position: absolute;;
  right: 5px;
 }
}

