@mixin transition($property: all,
  $duration: 0.45, $ease: cubic-bazier(0.65, 0, .076, 1)) {
  transition: $property $duration $ease;
}

button {
  display: inline-block;
  border-radius: 1.620rem;
  outline: none;
  border: 0;
  text-decoration: none;
  text-align: center;
  background-color: transparent;
  font-size: 33px;
  font-family: inherit;
}

.checkMenu {
  width: 20rem;
  height: auto;

  .circle {
    @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
    position: relative;
    display: block;
    margin: 0;
    width: 4rem;
    height: 4rem;
    background: rgb(0, 200, 255);
    border-radius: 2.125rem;
  }

  .icon {
    @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    background: white;

    &.arrow {
      @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
      left: 1.025rem;
      width: 1.125rem;
      height: 0.125rem;
      padding-right: 20px;
      background: none;

      &::before {
        position: absolute;
        content: '';
        top: -0.25rem;
        right: 0.0625rem;
        width: 0.625rem;
        height: 0.625rem;
        border-top: 0.125rem solid #fff;
        border-right: 0.125rem solid #fff;
        transform: rotate(45deg);
      }
    }
  }
}

.button-text {
  @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 5px 30px;
  margin: 0 auto;
  color: transparent;
  font-weight: 600;
  line-height: 1.6;
  text-align: center;
}

:hover {
  .circle {
    width: 100%;

    .icon {
      &.arrow {
        background: white;
        transform: translate(1rem, 0);
      }
    }
  }

  .button-text {
    color: white;
  }
}
@media only screen and (max-width: 690px) {
  button {
  display: inline-block;
  border-radius: 1.620rem;
  outline: none;
  border: 0;
  text-decoration: none;
  text-align: center;
  background-color: transparent;
  font-size: 25px;
  font-family: inherit;
}

.checkMenu {
  width: 17rem;
  height: auto;

  .circle {
    @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
    position: relative;
    display: block;
    margin: 0;
    width: 3rem;
    height: 3rem;
    background: rgb(0, 200, 255);
    border-radius: 1.5rem;
  }

  .icon {
    @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    background: white;

    &.arrow {
      @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
      left: 0.525rem;
      width: 1.125rem;
      height: 0.125rem;
      padding-right: 20px;
      background: none;

      &::before {
        position: absolute;
        content: '';
        top: -0.25rem;
        right: 0.0625rem;
        width: 0.625rem;
        height: 0.625rem;
        border-top: 0.125rem solid #fff;
        border-right: 0.125rem solid #fff;
        transform: rotate(45deg);
      }
    }
  }
}

.button-text {
  @include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 2px 30px;
  margin: 0 auto;
  color: transparent;
  font-weight: 600;
  line-height: 1.6;
  text-align: center;
}

:hover {
  .circle {
    width: 100%;

    .icon {
      &.arrow {
        background: white;
        transform: translate(1rem, 0);
      }
    }
  }

  .button-text {
    color: white;
  }
}
}
