.App {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  font-family:Arial, Helvetica, sans-serif;
}
body {
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 500px) {
  html,
  body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
  }
}
@media screen and (max-width: 735px) {
  html,
  body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
  }
}