.footer {
  width: 100%;
 min-height: 150px;
  background-color: #01457c;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 20px;
  font-size: 70px;
  cursor: pointer;
}
.socialMedia svg:hover {
  opacity: 50%;
  border: rgb(255, 255, 255) 1px solid;
}

.footer p {
  color: white;
}
@media screen and (max-height: 900px) {
  .socialMedia svg {
    font-size: 40px;
  }
}