.home {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.headerContainer {
  width: auto;
  padding-left: 100px;
  height: 85%;
}
.headerContainer h1 {
justify-content: center;
align-items: center;
font-size: 90px;
height: 230px;
font-weight: 600;
color: transparent;
padding-bottom: 120px;
background-image: linear-gradient(to right, #ffffff, #8ac4fe, #35c1fd, #bfc2f8, #000dff);
  -webkit-background-clip: text;
  background-clip: text;
  background-size: 200%;
  background-position: -100%;
  transition: all ease-in-out 2s;
  cursor: pointer;
}

 h1:hover {
  background-position: 200%;
}
/*Button style -> */
 * {
  box-sizing: border-box;
  &::before, &::after {
    box-sizing: border-box;
  }
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background-color: white;
  padding: 0;
  font-size: 25px;
  font-family: inherit;
}
&.checkMenu {
  width: 12rem;
  height: auto;
    .circle {
      position: relative;
      display: block;
      margin: 0;
      width: 3rem;
      height: 3rem;
      background: rgb(0, 0, 0);
      border-radius: 1.625rem;
    }
    .icon {
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      background: white;
    &.arrow {
        left: 0.625rem;
        width: 1.125rem;
        height: 0.125rem;
        background: none;
    
        &::before {
          
          content: '';
          top: -0.25rem;
          right: 0.0625rem;
          width: 0.625rem;
          height: 0.625rem;
          border-top: 0.125rem solid #fff;
          border-right: 0.125rem solid #fff;
          transform: rotate(45deg);
        }
      }
    }
    }
    
    .button-text {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.75rem 0;
      margin: 0 0 0 1.85rem;
      color: rgb(0, 0, 0);
      font-weight: 700;
      line-height: 1.6;
      text-align: center;
    }
    
    &:hover {
      .circle {
        width: 100%;
    
        .icon {
          &.arrow {
            background: white;
            transform: translate(1rem, 0);
          }
        }
      }
    
      .button-text {
        color: white;
      }
    }
    
@media only screen and (max-width: 900px) {
  .home  .headerContainer {
    height: 75%;
    padding-left: 70px;
  }
 .home .headerContainer h1 {
    font-size: 80px;
  }
}

@media only screen and (max-width: 650px) {
.home .headerContainer {
    height: 70%;
    padding-left: 50px;
  }

  .home .headerContainer h1 {
    font-size: 60px;
    height: 180px;
  }
}
    @media only screen and (max-width: 350px) {
      .home .headerContainer {
        height: 80%;
        padding-left: 40px;
      }
  
      .home .headerContainer h1 {
        font-size: 50px;
      }
}